/* Glassmorphism styles for login form */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.login-form {
  background: rgba(255, 255, 255, 0.1); /* Glassmorphism effect */
  backdrop-filter: blur(10px);
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border: 1px solid rgba(255, 255, 255, 0.2);
  width: 100%;
  max-width: 400px;
  text-align:left; /* Center align content */
}

/* Center the spinner */
.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Add some space between the spinner and the form */
  margin-top: 50px;
  margin-left: 100px;
}

/* Other styles */
.login-form h1 {
  color: white;
  margin-bottom: 30px;
}

.login-form .form-control {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.login-form .btn-primary {
  background-color: #ff00d0;
  border: none;
  padding: 10px;
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  transition: all 0.3s;
  margin-bottom: 15px;
  display: block;
  text-align: center;
}

.login-form .btn-primary:hover {
  background-color: rgba(62, 184, 255, 1);
}

.login-form .text-primary {
  color: #ff00d0;
  text-align: center;
  display: block;
}

.linkclass {
  margin-bottom: 2px;
}
